// navStackAndLongTap.js
			/* eslint-disable */
			export function navigateTo(router,pageUrl) {
				router.push(pageUrl);
			  }
			
			export function goBack(router) {
			    router.go(-1);
			}
			
			export function longTapNavigateTo(selector, targetPage, threshold = 500) {
			  let touchStartTime = 0;
			
			  const element = document.querySelector(selector);
			  if (!element) return;
			
			  element.addEventListener("touchstart", (e) => {
			    touchStartTime = Date.now();
			    e.preventDefault(); // prevent default link behavior
			  }, { passive: false });
			
			  element.addEventListener("touchend", () => {
			    const duration = Date.now() - touchStartTime;
			    if (duration >= threshold) {
			      console.log("Long tap detected!");
			      navigateTo(targetPage);
			    }
			  });
			
			  // Optional: support long mouse press
			  element.addEventListener("mousedown", (e) => {
			    touchStartTime = Date.now();
			    e.preventDefault();
			  });
			  element.addEventListener("mouseup", () => {
			    const duration = Date.now() - touchStartTime;
			    if (duration >= threshold) {
			        navigateTo(targetPage);
			    }
			  });
			
			}