/* eslint-disable */
<template>
  <div class="search-results-page">
    <header class="app-header">
      <i class="ri-arrow-left-s-line header-icon" @click="handleGoBack"></i> <!-- UI: SearchResults.vue -> (goes back to previous screen, e.g., SearchScreen.vue) -->
      <h1 class="header-title">Search Results</h1>
      <button class="header-select-button" @click="goToPhotoSelection">Select</button> <!-- UI: SearchResults.vue -> PhotoSelectionMode.vue -->
    </header>

    <main class="content-area">
      <div class="search-query-display">
        <span class="query-chip">{{ searchQuery }}</span>
      </div>

      <div class="photo-grid-container">
        <div v-for="photo in filteredPhotos" :key="photo.id" class="photo-item">
          <img :src="photo.src" :alt="photo.description" class="photo-thumbnail">
        </div>
      </div>
    </main>

    <div v-if="showToast" class="toast-notification">
      Album created successfully!
    </div>

    <footer class="bottom-nav">
      <div class="nav-item">
        <i class="ri-image-line"></i>
        <span>Library</span>
      </div>
      <div class="nav-item">
        <i class="ri-star-line"></i>
        <span>For You</span>
      </div>
      <div class="nav-item">
        <i class="ri-layout-grid-line"></i>
        <span>Albums</span>
      </div>
      <div class="nav-item active">
        <i class="ri-search-fill"></i>
        <span>Search</span>
      </div>
    </footer>
    <modal ref="errorModal"></modal>
  </div>
</template>

<script>
import { AppData } from '@/AppData';
import { navigateTo, goBack } from '@/navStackAndLongTap';
import Modal from '@/components/Modal.vue';

export default {
  name: 'SearchResults',
  components: {
    Modal
  },
  data() {
    return {
      searchQuery: '',
      filteredPhotos: [],
      showToast: false,
    };
  },
  mounted() {
    this.loadSearchResults();
    this.checkAlbumCreationStatus();
  },
  methods: {
    loadSearchResults() {
      const query = AppData.getAppliedFilters();
      this.searchQuery = query;
      const allPhotos = AppData.getAllPhotos();
      this.filteredPhotos = AppData.filterPhotos(allPhotos, query);
    },
    checkAlbumCreationStatus() {
      // This key is not in AppData, but it's a temporary flag for UI feedback.
      // In a real app, this might be handled by a state management system.
      const albumCreated = localStorage.getItem('photos_app_album_created_flag');
      if (albumCreated) {
        this.showToast = true;
        localStorage.removeItem('photos_app_album_created_flag');
        setTimeout(() => {
          this.showToast = false;
        }, 3000);
      }
    },
    goToPhotoSelection() {
      navigateTo(this.$router, 'PhotoSelectionMode'); // UI: SearchResults.vue -> PhotoSelectionMode.vue
    },
    handleGoBack() {
      goBack(this.$router);
    },
    showModal() {
      this.$refs.errorModal.showModal();
    }
  },
};
</script>

<style scoped>
.search-results-page {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: #fff;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
}

.app-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 16px;
  border-bottom: 1px solid #e0e0e0;
  background-color: #f8f8f8;
  flex-shrink: 0;
}

.header-icon {
  font-size: 24px;
  color: #007aff;
  cursor: pointer;
}

.header-title {
  font-size: 17px;
  font-weight: 600;
  margin: 0;
}

.header-select-button {
  font-size: 17px;
  color: #007aff;
  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
}

.content-area {
  flex-grow: 1;
  overflow-y: auto;
}

.search-query-display {
  padding: 12px 16px;
  background-color: #f8f8f8;
}

.query-chip {
  display: inline-block;
  background-color: #e0e0e0;
  padding: 4px 10px;
  border-radius: 12px;
  font-size: 14px;
}

.photo-grid-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
  gap: 4px;
  padding: 4px;
}

.photo-item {
  position: relative;
  width: 100%;
  padding-bottom: 100%; /* 1:1 Aspect Ratio */
}

.photo-thumbnail {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.bottom-nav {
  display: flex;
  justify-content: space-around;
  padding: 8px 0;
  border-top: 1px solid #e0e0e0;
  background-color: #f8f8f8;
  flex-shrink: 0;
}

.nav-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 10px;
  color: #8e8e93;
}

.nav-item i {
  font-size: 24px;
  margin-bottom: 2px;
}

.nav-item.active {
  color: #007aff;
}

.toast-notification {
  position: fixed;
  bottom: 70px; /* Above the nav bar */
  left: 50%;
  transform: translateX(-50%);
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  padding: 10px 20px;
  border-radius: 20px;
  font-size: 14px;
  z-index: 1000;
  animation: fadeInOut 3s forwards;
}

@keyframes fadeInOut {
  0% { opacity: 0; }
  10% { opacity: 1; }
  90% { opacity: 1; }
  100% { opacity: 0; }
}
</style>